.section-steps {
    padding: Min(96px, 9vw) 0;
    .section__header {
        padding-bottom: Min(100px, 6vw);
    }

}

.lines {
    position: relative;
    @media screen and (max-width: 992px) {
        background-color: $white;
    }
    .lines__bg{
        @media screen and (min-width: 992.99px) {
            position: absolute;
            top:0;
            left:0;
            z-index: -1;
            bottom:0;
            right:0;
            background-color: $white;
            @media screen and (min-width: 992.99px) {
                background: {
                    size: 110% auto;
                    repeat: no-repeat;
                    position: 80% 55%;
                    image: url('../img/general/bg-steps.webp');
                }
            }
        }
    }

}

.steps {
    .step-item {

        &:nth-child(2n) {
            .step-item__number {

                @media screen and (min-width: 992.99px) {
                    margin-left: Min(140px, 7vw);
                    order: 2;
                    margin-right: 0;
                }
            }
            .step-item__content {

                @media screen and (min-width: 992.99px) {
                    order: 1;
                }
                @media screen and (min-width: 1340.99px) {
                    margin-left: Min(160px, 7.5vw);
                }
            }
        }
        &:nth-child(3) {
            .step-item__number {
                &:after {
                    @media screen and (min-width: 992.99px) {
                        background-color: $white;
                    }
                }
            }
        }
    }
}

.step-item {
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: Min(96px, 9vw);

    @media screen and (max-width: 767.99px) {
        flex-direction: column;
        align-items: flex-start;
    }
    .step-item__text {
        font-size: 17px;
        line-height: 1.52;
        margin-bottom: 32px;
        @media screen and (max-width: 992px) {
            margin-bottom: 16px;
            font-size: 14px;
        }

    }
    small {
        color: $gray;
        font-size: 15px;
        line-height: 1.46;
        @media screen and (max-width: 992px) {
            font-size: 13px;
        }
    }
    .step-item__title {
        color: $primary;
        padding-bottom: 30px;
        margin-bottom: 30px;
        position: relative;
        text-transform: uppercase;

        @media screen and (max-width: 992px) {
            padding-bottom: 16px;
            margin-bottom: 16px;
        }
        &:after {
            content: '';
            background-color: $secondary;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 150px;
            height: 2px;
        }
    }
    .step-item__content {
        flex: 1 1 auto;
        max-width: 716px;
    }
    .step-item__number {
        flex: 0 0 auto;
        display: flex;
        align-items: flex-end;
        margin-right: Min(140px, 7vw);
        position: relative;

        @media screen and (max-width: 1340px) {
            svg {
                width: 250px;
            }
        }
        @media screen and (max-width: 992px) {
            svg {
                width: 200px;
            }
        }
        @media screen and (max-width: 767.99px) {
            margin-right: 0;
            margin-bottom: Min(140px, 7vw);
        }
        &:after {
            content: '';
            display: block;
            border-radius: 50%;
            background-color: rgb(242, 245, 246);
            width: 76px;
            height: 76px;
            margin-left: 26px;

            @media screen and (max-width: 1340px) {
                width: 60px;
                height: 60px;
            }
            @media screen and (max-width: 992px) {
                width: 50px;
                height: 50px;
            }
        }
        text {
            font-size: 280px;
            font-weight: 900;
            line-height: 1;
            text-transform: uppercase;
            font-family: 'Montserrat', sans-serif;
        }
    }
}

