*, *:before, *:after {
    box-sizing: border-box;
}

.d-none {
    display: none !important;
}

.svg-paints {
    visibility: hidden;
    height: 0;
    width: 0;
    position: absolute;
}

.btn {
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    padding: 15px 30px;
    border-radius: $border-radius;
    border-width: 1px;
    position: relative;
    border-style: solid;
    box-shadow: none !important;
    outline: none !important;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow: hidden;
    text-decoration: none !important;

    &:not(:disabled) {
        cursor: pointer;
    }

    @media screen and (max-width: 992px) {
        padding: 12px 20px;
    }
    span {
        position: relative;
        z-index: 2;
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 120%;
        padding-bottom: 120%;
        border-radius: 50%;
        transform: translate(-10px, -40%) scale(0.1);
        opacity: 0;
        z-index: 1;
        transition: transform 0.2s, opacity 0.2s, background-color 0.2s;
    }
    &:hover::after {
        opacity: 1;
        transform-origin: 100px 100px;
        transform: scale(1) translate(-10px, -40%);
    }
    &.btn-primary {
        background: $primary;
        border-color: $primary;
        color: $white;
        &:disabled {
            opacity: .8;
        }
        &:not(:disabled) {
            &::after {
                background-color: darken($primary, 10%);
            }
            &:hover {
                background-color: $primary;
                color: $white;
            }
        }
    }
    &.btn-outline-grey {
        background: transparent;
        border-color: #9A9A9A;
        color: #9A9A9A;
        &::after {
            background-color: darken(#9A9A9A, 10%);
        }
        &:hover {
            background-color: transparent;
            color: $white;
        }
    }
    &.btn-outline-light {
        background: transparent;
        border-color: $light;
        color: $light;
        &::after {
            background-color: rgba($white, .8);
        }
        &:hover {
            background-color: transparent;
            color: $black;
        }
    }
    &.btn-prev {
        background: transparent;
        border-color: #9A9A9A;
        color: $light;
        padding: 12px;
        &::after {
            content: none
        }
        span {
            display: block;
            width: 26px;
            height: 13px;
            background: {
                repeat: no-repeat;
                position: center center;
                size: 26px auto;
                image: url("data:image/svg+xml,%3Csvg width='26' height='12' viewBox='0 0 26 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.46967 5.46967C0.176777 5.76256 0.176777 6.23744 0.46967 6.53033L5.24264 11.3033C5.53553 11.5962 6.01041 11.5962 6.3033 11.3033C6.59619 11.0104 6.59619 10.5355 6.3033 10.2426L2.06066 6L6.3033 1.75736C6.59619 1.46447 6.59619 0.989592 6.3033 0.696699C6.01041 0.403806 5.53553 0.403806 5.24264 0.696699L0.46967 5.46967ZM26 5.25L1 5.25V6.75L26 6.75V5.25Z' fill='%239A9A9A'/%3E%3C/svg%3E%0A");
            }
        }
        &::after {
            background-color: rgba(#9A9A9A, .8);
        }
        &:hover {
            background-color: #9A9A9A;
            color: #ffffff;
            span {
                background: {
                    image: url("data:image/svg+xml,%3Csvg width='26' height='12' viewBox='0 0 26 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.46967 5.46967C0.176777 5.76256 0.176777 6.23744 0.46967 6.53033L5.24264 11.3033C5.53553 11.5962 6.01041 11.5962 6.3033 11.3033C6.59619 11.0104 6.59619 10.5355 6.3033 10.2426L2.06066 6L6.3033 1.75736C6.59619 1.46447 6.59619 0.989592 6.3033 0.696699C6.01041 0.403806 5.53553 0.403806 5.24264 0.696699L0.46967 5.46967ZM26 5.25L1 5.25V6.75L26 6.75V5.25Z' fill='%23FFFFFF'/%3E%3C/svg%3E%0A");
                }
            }
        }
    }
    &.btn-outline-primary {
        background: transparent;
        border-color: $primary;
        color: $primary;
        &::after {
            background-color: rgba($primary, .8);
        }
        &:hover {
            background-color: transparent;
            color: $white;
        }
    }
}

.info {
    width: 16px;
    height: 16px;
    background: #EDF0F1;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 50%;
    font-style: normal;
    font-size: 9px;
    color: #585858;
    position: relative;
    transform: translate(3px, -6px);
}

