.section-benefits {
    padding: Min(64px, 6vw) 0 0;

    @media screen and (max-width: 992px) {
        background-color: $white;
    }
    .section-benefits__bg{
        @media screen and (min-width: 992.99px) {
            position: absolute;
            background-color: $white;
            top:0;
            left:0;
            z-index: -1;
            bottom:0;
            right:0;
            background: {
                size: 100% auto;
                repeat: no-repeat;
                position: 50% 0;
                image: url("../img/general/bg-benefits.webp");
            }
        }
    }
    .section__header {
        padding-bottom: 52px;
    }
}

.benefits {
    display: flex;
    margin: 0 Max(-50px, -1.5vw);
    position: relative;
    justify-content: center;
    flex-wrap: wrap;

    @media screen and (max-width: 992px) {
        margin: 0 -15px;
    }
    /*
        @media screen and (max-width: 625px) {
            margin:0;
        }*/
}

@keyframes effect_dylan {
    0% {
        transform-origin: center center;
    }
    50% {
        transform: scale(1.5, 1.5);
        opacity: 0;
    }
    99% {
        transform: scale(0.001, 0.001);
        opacity: 0;
    }
    100% {
        transform: scale(0.001, 0.001);
        opacity: 1;
    }
}

.benefits-item {
    flex: 0 0 20%;
    width: 20%;

    padding: 0 Min(50px, 1.5vw) Min(64px, 5vw);
    font-size: 16px;
    position: relative;

    &:hover {
        outline: 0;
        .benefits-item__img:before {
            @media screen and (min-width: 992.99px) {
                animation: effect_dylan 1s ease-out;
            }
        }
    }
    @media screen and (max-width: 1380px) {

        font-size: 14px;
    }
    @media screen and (max-width: 1220px) {

        flex: 0 0 25%;
        width: 25%;
    }
    @media screen and (max-width: 992px) {

        flex: 0 0 33%;
        width: 33%;
        text-align: center;
    }
    @media screen and (max-width: 767.99px) {
        flex: 0 0 50%;
        width: 50%;
        padding: 0 15px 5vw;
    }

    /* @media screen and (max-width: 625px) {

         flex: 0 0 100%;
         width: 100%;
         display: flex;
         align-items: center;
         padding: 0 0 5vw;
     }*/
    .benefits-item__title {
        position: relative;
        z-index: 2;
    }
    .benefits-item__img {
        height: 76px;
        z-index: 2;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin: 0;
        position: relative;
        margin-bottom: 1.125rem;

        &:before {
            content: '';
            background-color: darken($info, 30%);
            border-radius: 50%;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            transform-origin: center center;
            z-index: 1;
            height: 76px;
            width: 76px;
            transform: scale(0.001, 0.001);
        }
        @media screen and (max-width: 992px) {
            justify-content: center
        }
        @media screen and (max-width: 767.99px) {
            height: 60px;
        }
        /* @media screen and (max-width: 625px) {

             margin-bottom: 0;
             margin-right: 16px;
             width: 60px;
             flex:0 0 60px;
         }*/
        > img {
            max-height: 100%;
            max-width: 100%;
            position: relative;
            z-index: 2;
        }
        > svg {
            max-height: 100%;
            max-width: 100%;
            position: relative;
            z-index: 2;
        }
    }
}
