.page__footer {
    background: $secondary;
    padding: Min(50px, 5vw) 0;
    max-width: 1920px;
    //font-family: 'Open Sans', sans-serif;
    margin: auto;
    width: 100%;
    overflow: hidden;
}

.footer {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin: 0 -10px;

    @media screen and (max-width: 992px) {
        flex-wrap: wrap;
    }
    > * {

        padding: 0 10px;
    }
}

.footer-logo {

    @media screen and (max-width: 992px) {
        flex: 1 1 100%;
        text-align: center;
        margin-bottom: 4vw;
    }
}

.footer-block {

    @media screen and (max-width: 768px) {
        flex: 1 1 100%;
        margin-bottom: 24px;
    }
}

.footer-block:nth-child(2) {

    @media screen and (min-width: 1340.99px) {
        margin-left: Min(110px, 5vw);
        margin-right: auto;
    }
}

.footer-block:last-child {
    @media screen and (min-width: 1340.99px) {
        margin-right: Min(80px, 4vw);
        margin-left: auto;
    }
}

.footer-contact {
    display: flex;
    color: #f2f2f2;
    font-weight: 400;
    font-size: 17px;

    @media screen and (max-width: 992px) {
        font-size: 15px;
    }

    @media screen and (max-width: 768px) {
        justify-content: center;
    }
    &:not(:last-child) {
        margin-bottom: 10px;
        @media screen and (min-width: 1340.99px) {
            margin-bottom: 16px
        }
    }
    p:not(:last-child) {
        margin-bottom: 10px;
        @media screen and (min-width: 1340.99px) {
            margin-bottom: 16px
        }
    }
    a {
        color: #f2f2f2;
        &:hover{

            color: #ffffff;
            border-bottom:1px solid #ffffff
        }
    }
    &.footer-contact__address {

        max-width: 305px;
        @media screen and (min-width: 1340.99px) {
            font-size: 18px;
        }

        @media screen and (max-width: 768px) {
            margin-left: auto;
            margin-right: auto;
            max-width: 100%;
            text-align: center;
            svg{
                display: none;
            }
        }

    }
    &.footer-contact__phone {
        font-size: 24px;

        @media screen and (max-width: 992px) {
            font-size: 18px;
        }
        a {
            text-decoration: none;
        }
    }
    .svg-icon {
        fill: #d3cbaa;
        flex: 0 0 auto;
        margin-top: .15em;
        margin-right: 20px;
    }
}
