.modal-choice {
    background: transparent;
    cursor: auto;
    padding: 0;

    overflow: visible;
    .fancybox-button.fancybox-close-small {
        color: #ffffff;
        padding: 0;
        right: 0;
        top: 0;
        opacity: 1;
        transform: translate(0, 100%);
        width: 24px;
        height: 24px;
        @media screen and (max-width: 992px) {

            transform: translate(0, -102%);
        }
        svg {
            width: 24px;
            height: 24px;
            @media screen and (max-width: 992px) {
                width: 18px;
                height: 18px;
            }
        }
        svg path {
            stroke-width: 2px;

        }
    }
}
.modal-choice-final {
    background: transparent;
    cursor: auto;
    padding: 0;

    overflow: visible;
    .fancybox-button.fancybox-close-small {
        color: #ffffff;
        padding: 0;
        right: 0;
        top: 0;
        opacity: 1;
        transform: translate(0, 100%);
        width: 24px;
        height: 24px;
        @media screen and (max-width: 992px) {

            transform: translate(0, -102%);
        }
        @media screen and (max-width: 625px) {

            transform: none;
            right: 5px;
            top: 5px;
        }

        svg {
            width: 24px;
            height: 24px;
            @media screen and (max-width: 992px) {
                width: 18px;
                height: 18px;
            }
        }
        svg path {
            stroke-width: 2px;
            @media screen and (max-width: 625px) {
                stroke: #000000;
            }
        }
    }
}

.modal.modal-calculator {
    overflow: visible;
    cursor: auto;
    background: transparent;
    max-width: calc(100% - 80px);

    @media screen and (max-width: 992px) {
        max-width: calc(100% - 60px);
    }
    @media screen and (max-width: 625px) {
        max-width: 100%;
    }
    .form-checkbox label:before {
        width: 25px;
        height: 25px;
        flex: 0 0 25px;

        @media screen and (min-width: 992.99px) {
            margin-right: 50px;
        }
    }
    .section__body {
        margin-right: 0
    }
    .fancybox-button.fancybox-close-small {
        color: #ffffff;
        padding: 0;
        right: 0;
        top: 0;
        opacity: 1;
        transform: translate(102%, -102%);
        width: 24px;
        height: 24px;
        @media screen and (max-width: 625px) {

            transform: none;
            right: 5px;
            top: 5px;
        }
        @media screen and (max-width: 992px) {

        }
        svg {
            width: 24px;
            height: 24px;
            @media screen and (max-width: 992px) {

                width: 18px;
                height: 18px;
            }
        }
        svg path {
            stroke-width: 2px;
            @media screen and (max-width: 625px) {
                stroke: #000000;
            }
        }
    }
}

.modal {
    display: none;
    padding: 0;
    .section__body {
        margin-left: 80px;
        margin-right: 40px;
        margin-top: 22px;

        @media screen and (max-width: 992px) {
            margin-left: 0;
            margin-right: 0;
        }

    }
}

.tooltipster-sidetip .tooltipster-box {
    padding: 14px 44px;
    background: #EDF0F1;
    border-radius: 5px;
    width: 630px;
    border: 0;
    margin-top: 24px;
    max-width: 50vw;
    @media screen and (max-width: 992px) {
        width: 300px;
        padding: 8px 14px;
    }
}

.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow-border {
    border-bottom-color: transparent;
}

.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow-uncropped {
    top: -15px;
}

.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow-background {
    border-bottom-color: #edf0f1;
}

.tooltipster-sidetip .tooltipster-content {
    font-size: 16px;
    line-height: 117.6%;
    color: #000000;
    margin: 0;
    padding: 0;
    @media screen and (max-width: 992px) {
        font-size: 12px;
    }
}

.modal-tabs {
    position: relative;
    width: 1230px;
    background: $white;
    max-width: 100%;
    border-radius: 5px;
    overflow: hidden;
}

.modal-body {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    max-width: 1304px;
    width: 100%;
    @media screen and (max-width: 625px) {
        flex-wrap: wrap;
    }
    .modal-item {
        width: calc(50% - 30px);
        flex: 0 0 calc(50% - 30px);
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 992px) {
            width: calc(50% - 15px);
            flex: 0 0 calc(50% - 15px);
        }
        @media screen and (max-width: 625px) {
            width: 100%;
            flex: 0 0 100%;
            margin-bottom: 30px;
        }
    }
    .modal-container {
        flex: 1 1 auto;
    }

}

.modal-item {
    position: relative;
    width: 100%;
    margin:0;
    max-width: 800px;
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 625px) {
        width: 100%;
    }
}

.modal-img {
    width: 100%;
    margin: 0;
    > img {
        width: 100%;
    }

    @media screen and (max-width: 625px) {
        display: none;
    }
}

.modal-container {
    background: #FFFFFF;
    padding: 30px;
    border-radius: 0 0 5px 5px;

    @media screen and (max-width: 992px) {

        padding: 16px;
    }
    @media screen and (max-width: 625px) {

        border-radius: 5px;
    }
}

.modal-btns {
    display: flex;
    align-items: stretch;
    .btn {
        //border-width: 1.5px
    }
    .btn:not(:last-child) {
        margin-right: 30px;
        @media screen and (max-width: 992px) {
            margin-right: 15px;
        }
    }

    @media screen and (max-width: 425px) {
        flex-direction: column;
        width: 100%;
        .btn:not(:last-child) {
            margin-right: 0;
            margin-bottom: 10px
        }
    }
}

.modal-table {
    margin-bottom: Min(60px, 4vw);
    &.disabled {
        .modal-table__row {
            * {
                color: #C2C2C2 !important
            }
        }
        .modal-table__checkbox .modal-table__check:before {
            background: #D7D7D7;
            border-color: #D7D7D7;
        }
    }
    .modal-table__row {
        display: flex;
        border-bottom: 1px solid #DCE1E4;
        align-items: center;
        &.modal-table__header {
            font-weight: 500;
            font-size: 12px;
            line-height: 117.6%;
            text-transform: uppercase;
            color: #646366;
            @media screen and (max-width: 992px) {
                font-size: 10px;
            }
            @media screen and (max-width: 525px) {
                font-size: 8px;
            }
            > div {
                padding-top: 12px;
                padding-bottom: 12px;
            }
        }
        > div {
            padding-top: 15px;
            padding-bottom: 15px;
        }
    }
}

.modal-table__checkbox {
    input {
        display: none;
    }
}

.modal-table__check {
    flex: 0 0 70px;

    @media screen and (max-width: 1240px) {

        flex: 0 0 30px;
    }
}

.modal-table__desc {
    flex: 1 1 auto;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0;
    p:last-child {
        margin-bottom: 0;
    }
}

.modal-table__price {
    text-align: right;
    flex: 0 0 125px;
}

.modal-table__checkbox {
    cursor: pointer;
    .modal-table__check {
        &:before {
            content: '';
            display: block;
            height: 22px;
            width: 22px;
            background: #F6F9FC;
            border: 1.5px solid #646366;
            border-radius: 20px;
        }
    }
    input:checked ~ label .modal-table__check::before {
        border-color: #D63B46;
        background: {
            color: #D63B46;
            repeat: no-repeat;
            position: center center;
            size: 13px auto;
            image: url("data:image/svg+xml,%3Csvg width='13' height='10' viewBox='0 0 13 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M12.4359 0.340855C12.6793 0.5907 12.6779 0.989494 12.4327 1.23763L5.17465 8.58187C5.17447 8.58207 5.17447 8.58237 5.17466 8.58257C5.17485 8.58276 5.17485 8.58306 5.17466 8.58325L4.73654 9.0325C4.48696 9.28841 4.07555 9.28839 3.82599 9.03246L0.248083 5.36318C0.0057403 5.11465 0.00568062 4.71824 0.247949 4.46963C0.499139 4.21187 0.9134 4.21183 1.16464 4.46955L3.56935 6.93624C3.95998 7.33693 4.60337 7.33912 4.9967 6.94108L11.522 0.337675C11.7739 0.0828326 12.1858 0.0842659 12.4359 0.340855Z' fill='white'/%3E%3C/svg%3E%0A");

        }
    }
    input:not(:disabled) ~ label {
        cursor: pointer;
    }
    .modal-table__desc {
        font-size: 18px;
        line-height: 117.6%;
        color: #000000;

        @media screen and (max-width: 992px) {

            font-size: 14px;
        }
        @media screen and (max-width: 525px) {

            font-size: 12px;
        }
        .modal-table__level {
            font-weight: 500;
            font-size: 10px;
            line-height: 117.6%;
            text-transform: uppercase;
            color: #959595;
            margin: 0;
        }
    }

    .modal-table__price {
        font-weight: 500;
        font-size: 18px;
        line-height: 117.6%;
        color: #000000;
        @media screen and (max-width: 992px) {
            font-size: 14px;
            flex: 0 0 100px
        }
        @media screen and (max-width: 525px) {

            font-size: 12px;
            flex: 0 0 80px
        }
    }
}

.modal-answer {
    max-width: 500px;
    background: #ffffff;
    border-radius: 5px;
    padding: 20px;
    .modal-answer__title {
        font-size: 18px;
        line-height: 1.23;
        padding-right: 20px;
    }
}

.modal-tabs {
    .form-group:not(.form-checkbox) {
        max-width: 500px;
        width: 100%;
    }
    .modal-tab {
        display: none;
    }
    #modal-tab-1 {
        display: block;
    }
    #modal-designing-tab-1 {
        display: block;
    }
    .modal-body {
        display: flex;
        align-items: stretch;
    }
    .modal-container {
        padding: 60px 30px 60px 60px;
        @media screen and (max-width: 1240px) {
            padding: 40px 30px;
        }
        @media screen and (max-width: 625px) {
            padding: 32px 16px;
        }
    }
    .modal-step {
        width: auto;
        flex: 0 0 auto;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        padding: 17px 1px;
        position: relative;
        font-weight: 500;
        font-size: 150px;
        @include font-r(150, 18);
        overflow: hidden;
        line-height: .7;
        color: rgba(#000000, .2);

        @media screen and (max-width: 625px) {
            display: none;
        }
        &:before {
            content: none;
            width: 100%;
            height: 105px;
            display: block;
            background: {
                repeat: no-repeat;
                position: center 100%;
                size: contain;
            }
        }
        &:not(:first-child) {
            margin-left: 1px;
        }
        &:not(.active) {
            background: linear-gradient(0deg, #023D71 0%, #6DB4DC 100%);
        }
        &.modal-step-one {
            &:before {
                background-image: url("data:image/svg+xml,%3Csvg width='39' height='105' viewBox='0 0 39 105' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath opacity='0.2' d='M38.55 0V105H23.7V13.05H0V0H38.55Z' fill='black'/%3E%3C/svg%3E%0A");

            }
