        &.modal-step-two {
            &:before {
                background-image: url("data:image/svg+xml,%3Csvg width='79' height='107' viewBox='0 0 79 107' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath opacity='0.2' d='M79.0001 93.9501V107H3.5501V96.8L46.3001 55.55C51.5001 50.55 55.0001 46.25 56.8001 42.65C58.6001 38.95 59.5001 35.25 59.5001 31.55C59.5001 26.05 57.5501 21.8 53.6501 18.8C49.8501 15.7 44.3501 14.15 37.1501 14.15C25.5501 14.15 16.6001 17.95 10.3001 25.55L0.100098 16.7C4.2001 11.7 9.5501 7.80005 16.1501 5.00005C22.8501 2.20005 30.3001 0.800049 38.5001 0.800049C49.5001 0.800049 58.2501 3.45005 64.7501 8.75004C71.2501 13.95 74.5001 21.05 74.5001 30.05C74.5001 35.65 73.3001 40.9501 70.9001 45.9501C68.5001 50.9501 63.9501 56.65 57.2501 63.05L25.1501 93.9501H79.0001Z' fill='black'/%3E%3C/svg%3E%0A");
            }
        }
        &.modal-step-three {
            &:before {
                background-image: url("data:image/svg+xml,%3Csvg width='80' height='107' viewBox='0 0 80 107' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath opacity='0.2' d='M47.4 44.1C57.7 45.1 65.55 48.3 70.95 53.7C76.35 59 79.05 65.8 79.05 74.1C79.05 80.1 77.55 85.55 74.55 90.45C71.55 95.25 67.05 99.1 61.05 102C55.15 104.8 47.9 106.2 39.3 106.2C31.8 106.2 24.6 105.1 17.7 102.9C10.8 100.6 5.15 97.45 0.75 93.45L7.65 81.6C11.25 85 15.85 87.75 21.45 89.85C27.05 91.85 33 92.85 39.3 92.85C47.1 92.85 53.15 91.2 57.45 87.9C61.85 84.6 64.05 80.05 64.05 74.25C64.05 68.45 61.9 63.95 57.6 60.75C53.3 57.55 46.8 55.95 38.1 55.95H29.7V45.45L56.1 13.05H5.55V0H74.85V10.2L47.4 44.1Z' fill='black'/%3E%3C/svg%3E%0A");
            }
        }
        &.modal-step-four {
            &:before {
                background: {
                    image: url("data:image/svg+xml,%3Csvg width='39' height='105' viewBox='0 0 39 105' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath opacity='0.2' d='M38.55 0V105H23.7V13.05H0V0H38.55Z' fill='black'/%3E%3C/svg%3E%0A");
                }
            }
        }
    }

}

.modal-results {
    margin-top: Min(108px, 5vw);
    dl {
        display: flex;
        align-items: stretch;
        font-size: 18px;
        justify-content: space-between;
        line-height: 22px;

        @media screen and (max-width: 992px) {

            font-size: 15px;
        }
        @media screen and (max-width: 425px) {

            flex-direction: column;
        }
        &:before {

            @media screen and (min-width: 425.99px) {
                content: '';
                position: relative;
                flex: 1 1 auto;
                display: block;
                order: 2;
                border-bottom: 1px solid #DCE1E4;
            }
        }
    }
    dt {
        color: #585858;
        background: #ffffff;
        display: inline-flex;
        order: 1;
        font-weight: normal;
        flex: 0 0 auto;
        padding-right: 10px;
        margin: 0;

        .info {
            margin-left: 3px;
            margin-right: -10px;
        }
    }
    dd {

        background: #ffffff;
        order: 3;
        margin: 0;
        flex: 0 0 auto;
        font-weight: normal;
        color: #000000;
        padding-left: 10px;

        @media screen and (max-width: 425px) {
            padding-left: 0;
        }
        @media screen and (min-width: 425.99px) {
            text-align: right;
        }
    }
    .modal-results__summ {
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        text-transform: uppercase;
        margin: Min(50px, 6vw) 0 Min(70px, 7vw);

        color: #4B4B4B;

        @media screen and (min-width: 425.99px) {
            text-align: right;
        }
        span {

            font-weight: 600;
            color: #000000;
        }
    }
}

