.section-main {
    padding: Min(54px, 5vw) 0 0;

    @media screen and (max-width: 992px) {
        background-color: $info;
    }
    .section-main__bg{
        @media screen and (min-width: 992.99px) {
            position: absolute;
            top:0;
            left:0;
            background-color: $info;
            z-index: -1;
            bottom:0;
            right:0;
            background: {
                size: 100% auto;
                repeat: no-repeat;
                position: 100% 0%;
                image: url('../img/general/bg-main.webp');
            }
        }
    }


    .section__header {
        padding-bottom: Min(54px, 5vw);
    }

}

.categories {
    display: flex;
    align-items: stretch;
    position: relative;

    @media screen and (max-width: 992px) {
        flex-wrap: wrap;
    }
    @media screen and (min-width: 992.99px) {
        margin: 0 -10px;
    }
}

.categories-item {
    flex: 0 0 50%;
    position: relative;
    color: $light;
    cursor: default;
    padding: 0 10px;

    @media screen and (max-width: 992px) {
        flex: 0 0 100%;
        padding:0 15px 15px;
    }
    .categories-item__title {
        font-family: 'Cormorant Garamond', serif;
        @include font-r(60, 30);
        margin-bottom: 6px;
        line-height: 1.2;
        @media screen and (min-width: 1500.99px) {
            margin-bottom: 16px;
        }
    }
    .categories-item__text {
        font-size: 16px;
        line-height: 1.5;
        opacity: 0;

        height: calc(3em * 1.5);
        margin-bottom: 6px;
        margin-top:calc(-3em * 1.5);
        transition: opacity .3s, margin-top .3s;
        //position: absolute;
        overflow: hidden;

        @media screen and (min-width: 1500.99px) {
            width: 90%;
        }

        @media screen and (max-width: 992px) {
            opacity: 1;
            transform: none;
            height: auto;
            margin:0;
        }
        &.open{
            opacity: 1;
            transform: none;
            margin-top:0;
            position: relative;
            transition: opacity .3s, margin-top .3s;
        }
    }
    @media screen and (min-width: 1340.99px) {
        &:first-child {
            .categories-item__body {
                padding-left: calc(Min(2.6vw, 50px) + var(--container-padding))
            }
        }
        &:last-child {
            .categories-item__body {
                padding-right: calc(Min(2.6vw, 50px) + var(--container-padding))
            }
        }
    }
    .categories-item__body {
        min-height: Min((calc((50vw - 10px) * .6)), 430px);
        padding: 9% var(--container-padding) 16px;

        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        background: {
            repeat: no-repeat;
            size: cover;
        }
        border-bottom-right-radius: $border-radius;
        border-bottom-left-radius: $border-radius;

        @media screen and (min-width: 1500.99px) {
            padding: 9% 30px 30px;
        }
        @media screen and (max-width: 1340px) {
            min-height: 400px;
        }
        @media screen and (max-width: 992px) {
            border:0;
            min-height: 0;
            padding: 30vw var(--container-padding) 16px;
        }
    }
    &.categories-item_construction {
        .categories-item__body {
            background-image: url('../img/general/construction.webp');
            background-position: 100% 0;
            @media screen and (max-width: 992px) {
                background-position: 50% 0;
                background-image: url('../img/general/construction-mb.webp')
            }
        }
    }
    &.categories-item_designing {
        .categories-item__body {
            background-position: 0 0;
            background-image: url('../img/general/designing.webp');
            @media screen and (max-width: 992px) {
                background-position: 50% 0;
                background-image: url('../img/general/designing-mb.webp')
            }
        }
    }
    .categories-item__btns {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;

        @media screen and (max-width: 425px) {
            flex-wrap: wrap;
        }
        a {
            margin-right: 20px;
            margin-top: 10px;
            @media screen and (max-width: 425px) {

                margin-right: 0;
                width: 100%;
            }
        }
    }
}
