header {
    background: $secondary;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 99;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    position: relative;

    @media screen and (max-width: 1220px) {
        padding-right: 70px;
    }
}

.header-burder {
    display: none;
    @media screen and (max-width: 1220px) {
        display: block;
        width: 45px;
        height: 28px;
        position: absolute;
        right: 0;
        top: 50%;
        transform: rotate(0deg) translateY(-50%);
        transition: .5s ease-in-out;
        cursor: pointer;
        z-index: 101;
        &:before, &:after {
            content: '';
        }
        span, &:before, &:after {
            display: block;
            position: absolute;
            height: 2px;
            width: 100%;
            background: #ffffff;
            border-radius: $border-radius;
            opacity: 1;
            left: 0;
            transform: rotate(0);
            transition: .25s ease-in-out;
        }
        &:before {
            top: 0;
            transform-origin: left center;
        }
        span {
            top: 50%;
            transform: rotate(0);
            transform-origin: left center;
        }
        &:after {
            top: 100%;
            transform-origin: left center;
        }
        &.open:before {
            transform: rotate(45deg) translateY(-50%);
            top: -14%;
            left: 13.3%;
        }
        &.open span {
            width: 0;
            opacity: 0;
        }
        &.open {

        }
        &.open:after {
            transform: rotate(-45deg);
            top: 92%;
            left: 13.3%;
        }
    }

    @media screen and (max-width: 625px) {
        width: 40px;

        &.open:before {
            transform: rotate(45deg) translateY(-50%);
            top: -8%;
            left: 13.3%;
        }
    }
}

@media screen and (max-width: 992px) {
    br {
        display: none;
    }
}

.header-logo {
    @media screen and (min-width: 992.99px) {
        width: Min(8vw, 150px);
        flex: 0 0 auto;
    }
    @media screen and (max-width: 625px) {
        img {
            width: 60px;
        }
    }
}

.header-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    list-style-type: none;
    padding: 0;
    margin: 0;

    @media screen and (max-width: 1220px) {
        display: none;
        &.open {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: $secondary;
            z-index: 100;
        }
    }
    .header-menu__item {
        padding: 10px 16px 6px;
        text-align: center;
        font-size: 12px;
        color: $white;
        @media screen and (min-width: 625.99px) {
            &.header-menu__item_mb {
                display: none;
            }
        }
        &.header-menu__item_mb {
            padding-bottom: 0;
            &:not(:last-child) {
                margin-top: 40px;
            }
        }
        @media screen and (min-width: 1220.99px) {
            flex: 1 1 auto;
            padding: 16px 1.875rem 12px;
        }
        @media screen and (max-width: 1500px) {
            padding-right: 0;
        }

        a {
            color: $white;
            &:hover,
            &:focus {
                text-decoration: none;
                color: $white;
            }
        }
    }
    .header-contacts__phone {
        font-size: 20px;
        font-weight: 600;

    }
    .header-menu__link {
        font-weight: bold;
        text-transform: uppercase;
        line-height: 1.2;
        position: relative;
        padding-bottom: 4px;
        &:after {
            display: block;
            content: "";
            background-color: $white;
            height: 1px;
            width: 0;
            bottom: 0;
            left: 50%;
            position: absolute;
            transition: width .2s ease-in-out;
            transform: translateX(-50%);
        }
        &:hover:after,
        &:focus:after {
            width: 100%;
        }
    }
}

.header-contacts {
    text-align: right;

    margin-right: 0;
    margin-left: auto;

    @media screen and (max-width: 625px) {
        display: none;
    }
    .header-contacts__address {
        color: $white;
        margin: 0;
        font-size: 15px;
    }
    .header-contacts__phone {
        font-size: 24px;
        font-weight: 500;
        color: $white;
        margin-bottom: 12px;
        line-height: 1;
        white-space: nowrap;
        display: block;
        @media screen and (max-width: 1220px) {
            font-size: 20px;
            margin-bottom: 6px;
        }
        &:hover,
        &:focus {
            text-decoration: none;
            color: $white;
        }
    }
}
