// Mixins.

@mixin nl {
    margin: 0;
    padding: 0;
    text-indent: 0;
    list-style: none;
    list-style-position: outside;
}

@mixin cf {
    &:before,
    &:after {
        display: table;
        content: "";
    }

    &:after {
        clear: both;
    }

    .lt-ie8 & {
        zoom: 1;
    }
}

@mixin blocks-justify {
    text-align: justify;
    text-justify: newspaper;
}


@mixin font-px ($px) {
    font-size: $px / 16 + rem !important;
}

@function strip-units($number) {
    @return $number / ($number * 0 + 1);
}

@mixin font-r($maxFz, $minFz,  $weight:false, $color:false) {
    $minRem: $minFz / 16 + rem;
    $maxRem: $maxFz / 16 + rem;

    $raznostFz: strip-units($maxFz - $minFz);

    @media (max-width: 320px) {
        font-size: $minRem
    }

    @media screen and (min-width: 1500px){
        font-size: $maxRem
    }


    @media screen and (max-width: 1500px) and (min-width: 320px){
        font-size: calc(#{$minRem} + #{$raznostFz} * (100vw - 320px) / 1200)
    }
    @if $weight {
        font-weight: $weight;
    }
    @if $color {
        color: $color;
    }
}

