.section-request {
    padding-bottom: Min(192px, 11vw);
    background: {
        size: cover;
        position: 50% 0;
        image: url('../img/general/bg-request.webp');
        repeat: no-repeat;
    }
    .section__header {
        margin-bottom: Min(64px, 7vw);
    }
}

.request {
    width: 100%;
    max-width: 618px;
    border-radius: $border-radius;
    padding: Min(50px, 6vw) Min(60px, 6vw);
    background: {
        color: #f2f5f6;
        size: 100% auto;
        position: 50% 100%;
        image: url('../img/general/bg-request-form.webp');
    }
    background-repeat: no-repeat;
    .btn{
        @media screen and (max-width: 425px) {
            width: 100%;
        }
    }
}
