html, body {
  height: 100%;
}

.page {
  font-family: 'Montserrat', sans-serif;
  max-width: 1920px;
  width: 100%;
  margin: 0 auto !important;
  overflow: auto !important;
  color: #000;
  display: flex;
  flex-direction: column;
}

.page.open {
  max-width: 100%;
  max-height: 100%;
  overflow: hidden;
}

.page__footer {
  flex: 0 0 auto;
}

.page__wrapper {
  flex: 1 0 auto;
  width: 100%;
}

* html .page__wrapper {
  height: 100%;
}

:root {
  --container-padding: Min(3.64vw, 70px);
  --section-header: 150px;
}

@media screen and (max-width: 1340px) {
  :root {
    --container-padding: 2vw;
    --section-header: 80px;
  }
}

@media screen and (max-width: 992px) {
  :root {
    --container-padding: 15px;
    --section-header: 80px;
  }
}

p {
  margin-top: 0;
}

a {
  text-decoration: none;
}

.container-fluid {
  max-width: calc(100% - 2 * var(--container-padding));
  margin: auto;
  width: 100%;
  overflow: hidden;
}

.section {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.section__body {
  position: relative;
}

@media screen and (min-width: 992.99px) {
  .section__body {
    margin: 0 calc(1.875rem + Min(8vw, 150px));
  }
}

@media screen and (min-width: 767.99px) {
  .column-text {
