.header-burder {
  display: none;
}

@media screen and (max-width: 1220px) {
  .header-burder {
    display: block;
    width: 45px;
    height: 28px;
    position: absolute;
    right: 0;
    top: 50%;
    transform: rotate(0deg) translateY(-50%);
    transition: .5s ease-in-out;
    cursor: pointer;
    z-index: 101;
  }
  .header-burder:before, .header-burder:after {
    content: '';
  }
  .header-burder span, .header-burder:before, .header-burder:after {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background: #ffffff;
    border-radius: 3px;
    opacity: 1;
    left: 0;
    transform: rotate(0);
    transition: .25s ease-in-out;
  }
  .header-burder:before {
    top: 0;
    transform-origin: left center;
  }
  .header-burder span {
    top: 50%;
    transform: rotate(0);
    transform-origin: left center;
  }
  .header-burder:after {
    top: 100%;
    transform-origin: left center;
  }
  .header-burder.open:before {
    transform: rotate(45deg) translateY(-50%);
    top: -14%;
    left: 13.3%;
  }
  .header-burder.open span {
    width: 0;
    opacity: 0;
  }
  .header-burder.open:after {
    transform: rotate(-45deg);
    top: 92%;
    left: 13.3%;
  }
}

@media screen and (max-width: 625px) {
  .header-burder {
    width: 40px;
  }
